@import url('https://fonts.googleapis.com/css2?family=Ribeye+Marrow&family=Rubik+Moonrocks&display=swap');

* {
  margin: 0;
  padding: 0;
	/* font-family: 'Lobster', cursive; */
  font-family: 'Helvetica Neue', sans-serif;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.App {
  position: absolute;
  font-size: 16px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  background-image: url('./poster.png');
  background-size: contain;
}

.main {
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 270px;
}

.title {
  width: 80%;
  max-width: 666px;
  margin-top: -8px;
  margin-bottom: 24px;
  user-select: none;
}

.img {
  width: 100%;
}

.card {
  position: relative;
  width: 80%;
  min-width: 256px;
  max-width: 666px;
  height: 216px;
}

.container {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 12px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #fff7f8;
  background: #fff;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.126),
    0 1px 2px rgba(0, 0, 0, 0.179), 0 2px 4px rgba(0, 0, 0, 0.224),
    0 4px 8px rgba(0, 0, 0, 0.277), 0 10px 20px rgba(0, 0, 0, 0.4);
  transition: background-color 0.8s ease-in-out;
}

.toggled .container {
  background-color: #307257;
}

.form {
  z-index: 1;
  position: absolute;
  left: 0;
  top: 12px;
  bottom: 6px;
  width: 76%;
  box-sizing: border-box;
  padding: 20px 12px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  transition: transform .8s ease-in-out,
    opacity .8s ease-in-out;
}

.toggled .form {
  margin-right: 0;
	transform: translateY(32px);
	opacity: 0;
}

.input {
  font-size: 14px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fff;
  border-radius: 22px;
  padding: 10px 12px;
  border: 2px solid #307257;
}

.focused {
  border-color: #ee4d2d;
}

input {
  outline: none;
  border: 0;
  height: 24px;
  width: 60%;
  min-width: 72px;
  padding-left: 12px;
  font-family: 'Helvetica Neue', sans-serif;
  caret-color: #307257;
  background-color: transparent;
}

.suffix {
  font-family: 'Helvetica Neue', sans-serif;
}

.error {
  margin-left: 8px;
  margin-top: 8px;
  text-align: left;
  font-size: 12px;
  color: #D30F17;
  animation: errtips 0.8s linear;
}

@keyframes errtips {
  10% {
    transform: translateY(1px);
  }
  20% {
    transform: translateY(-0.5px);
  }
  30% {
    transform: translateY(1px);
  }
  40% {
    transform: translateY(-0.5px);
  }
  50% {
    transform: translateY(1px);
  }
  60% {
    transform: translateY(-0.5px);
  }
  70% {
    transform: translateY(1px);
  }
  80% {
    transform: translateY(-0.5px);
  }
  90% {
    transform: translateY(1px);
  }
  100% {
    transform: translateY(0);
  }
}

.footer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.button {
  cursor: pointer;
	display: inline-block;
  box-sizing: border-box;
	min-width: 100px;
  padding: 12px 24px;
	font-size: 13px;
	line-height: 18px;
	text-decoration: none;
	color: #FFF;
	background: #ee4d2d;
	outline: none;
	border-radius: 12px;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
	position: relative;
	overflow: visible; /* IE9 & 10 */
	-webkit-transition: 250ms linear;
	   -moz-transition: 250ms linear;
	     -o-transition: 250ms linear;
					transition: 250ms linear;
}

.button::before {
	content: '';
	display: block;
	position: absolute;
	top: -7px;
	left: -3px;
	right: 0;
	height: 23px;
	background: url(data:image/png;base64,) no-repeat 0 0,
    url(data:image/png;base64,) no-repeat 50% 0,
    url(data:image/png;base64,) no-repeat 100% 0;
}

.button:active {
  box-shadow: rgba(0, 0, 0, 0.4) 0px 1px 2px, rgba(0, 0, 0, 0.3) 0px 3px 6px -1px, rgba(0, 0, 0, 0.2) 0px -1px 0px inset;
}

.colorful {
  position: absolute;
  left: 80%;
  top: 20px;
  bottom: 20px;
  width: 16%;
  flex-shrink: 0;
  box-sizing: border-box;
  border-radius: 12px;
  background-image: url('./ballon.png');
  background-color: #307257;
  background-size: cover;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.126),
    0 1px 2px rgba(0, 0, 0, 0.179), 0 2px 4px rgba(0, 0, 0, 0.224),
    0 4px 8px rgba(0, 0, 0, 0.277), 0 10px 20px rgba(0, 0, 0, 0.4);
  transition: left .8s ease-in-out, background-color .8s ease-in-out;
}

.toggled .colorful {
  background-color: #fff;
  left: 12px;
}

.result {
  z-index: -1;
  cursor: pointer;
  position: absolute;
  top: 20px;
  bottom: 20px;
  right: 12px;
  width: calc(76% + 12px);
  opacity: 0;
  color: #fff;
  display: flex;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  transform: translateX(48px);
  transition: transform .8s ease-in-out, opacity .8s ease-in-out;
}

.toggled .result {
  opacity: 1;
  z-index: 2;
  transform: translateX(0);
}

.content {
  flex: 1;
  opacity: 0;
  width: 0;
}

.toggled .content {
  opacity: 1;
  width: auto;
  margin-left: 12px;
}


.text {
  padding: 0px 16px;
}

.desc {
  text-align: left;
  font-size: 18px;
}

.number {
  text-align: center;
  font-size: 88px;
  /* text-shadow: -1px -2px #ee4d2d, 1px 2px #D30F17; */
  font-family: 'Rubik Moonrocks', cursive;
  animation: animate 0.5s linear infinite;
}

@keyframes animate {
  0%, 100% {
    text-shadow: -1.5px 2px 0 #307257, 1px 2px 0 #D30F17;
  }
  25% {
    text-shadow: 1px 2px 0 #D30F17, -1.5px 2px 0 #ee4d2d;
  }
  50% {
    text-shadow: 1.5px 2px 0 #307257, 1px 2px 0 #D30F17;
  }
  75% {
    text-shadow: -1px 2px 0 #D30F17, -1.5px 2px 0 #ee4d2d;
  }
}

hr {
  border: 0;
  padding-top: 1.5px;
  background: linear-gradient(to right, transparent, #fff, transparent);
}

.subtext {
  color: #efefef;
  padding: 8px 16px 0;
  font-size: 12px;
  text-align: left;
}
