.ballon1 {
  height: 108px;
  position: absolute;
  top: 0;
  left: 5%;
  animation: swing 1.2s ease-in-out infinite;
  transform: rotate(-3deg);
  transform-origin: top center;
}

.ballon2 {
  height: 48px;
  position: absolute;
  top: 0;
  left: 24%;
  animation: swing 1.2s ease-in-out infinite;
  transform: rotate(-3deg);
  transform-origin: top center;
}

.ballon3 {
  height: 60px;
  position: absolute;
  top: 0;
  right: 30%;
  animation: swing 1.2s ease-in-out infinite;
  transform: rotate(-3deg);
  transform-origin: top center;
}

.ballon4 {
  height: 92px;
  position: absolute;
  top: 0;
  right: 2%;
  animation: swing 1.2s ease-in-out infinite;
  transform: rotate(-3deg);
  transform-origin: top center;
}

@keyframes swing {
  0% { transform: rotate(-5deg);}
  50% { transform: rotate(5deg);}
  100% { transform: rotate(-5deg);}
}

.tree {
  width: 96px;
  margin-top: 7%;
  position: relative;
}

.tree-top {
  width: 28px;
  position: absolute;
  top: 6px;
  left: calc(50% - 14px);
  z-index: 1;
  animation: spin 2.4s linear infinite;
}

.trunk {
  width: 100%;
  top: 0;
  left: 0;
}

.bulb {
  animation: bulb 0.55s ease-in-out infinite alternate;
}

@keyframes bulb {
  0% {
    fill-opacity: 0.6;
  }
  100% {
    fill-opacity: 1.0
  }
}

@keyframes spin {
  from {transform: rotate(0deg);}
  to {transform: rotate(360deg);}
}

.snow {
  width: 64px;
  position: absolute;
  top: 35%;
  right: 10%;
  z-index: 1;
